import React, {Dispatch, SetStateAction} from 'react';
import {Photo} from "../../../types";
import {PhotoItem} from "./PhotoItem";
import {CircularProgress} from "@mui/material";

interface Props {
    photos: Photo[];
    isLoading: boolean;
    setPhotos: Dispatch<SetStateAction<Photo[]>>;
    currentPage: number;
    currentItems: number;
    setOpen: Dispatch<SetStateAction<boolean>>;
    setModalImage: Dispatch<SetStateAction<string>>;
}

export const PhotoList = ({photos, isLoading, setPhotos, currentPage, currentItems, setOpen, setModalImage}: Props) => {
    return (
        <div style={{display: 'flex', flexWrap: 'wrap', alignItems: 'center', justifyContent: 'center', flexGrow: '1'}}>
            {
                isLoading ? <CircularProgress color="secondary" /> :
                    photos && (
                        photos.slice(currentItems * currentPage - currentItems, currentItems * currentPage).map(
                            (photo: Photo) => (
                                <PhotoItem photo={photo} key={photo.id} photos={photos} setPhotos={setPhotos} setOpen={setOpen} setModalImage={setModalImage}/>
                            )
                        )
                    )
            }
        </div>
    );
};